module.exports = "uniform float u_time;\nuniform float u_alpha;\nvarying vec3 v_uv;\nconst int MAX_OCTAVES = 6;\nconst float octaves = 6.0;\nvec4 mod289(vec4 x) {\n\treturn x - (floor(x * (1.0 / 289.0)) * 289.0);\n}\nvec4 permute(vec4 x) {\n\treturn mod289(((x * 34.0) + 1.0) * x);\n}\nvec4 taylorInvSqrt(vec4 r) {\n\treturn 1.79284291400159 - (0.85373472095314 * r);\n}\nvec2 fade(vec2 t) {\n\treturn ((t * t) * t) * ((t * ((t * 6.0) - 15.0)) + 10.0);\n}\nfloat cnoise(vec2 P) {\n\tvec4 Pi = floor(P.xyxy) + vec4(0.0, 0.0, 1.0, 1.0);\n\tvec4 Pf = fract(P.xyxy) - vec4(0.0, 0.0, 1.0, 1.0);\n\tPi = mod289(Pi);\n\tvec4 ix = Pi.xzxz;\n\tvec4 iy = Pi.yyww;\n\tvec4 fx = Pf.xzxz;\n\tvec4 fy = Pf.yyww;\n\tvec4 i = permute(permute(ix) + iy);\n\tvec4 gx = (fract(i * (1.0 / 41.0)) * 2.0) - 1.0;\n\tvec4 gy = abs(gx) - 0.5;\n\tvec4 tx = floor(gx + 0.5);\n\tgx = gx - tx;\n\tvec2 g00 = vec2(gx.x, gy.x);\n\tvec2 g10 = vec2(gx.y, gy.y);\n\tvec2 g01 = vec2(gx.z, gy.z);\n\tvec2 g11 = vec2(gx.w, gy.w);\n\tvec4 norm = taylorInvSqrt(vec4(dot(g00, g00), dot(g01, g01), dot(g10, g10), dot(g11, g11)));\n\tg00 *= norm.x;\n\tg01 *= norm.y;\n\tg10 *= norm.z;\n\tg11 *= norm.w;\n\tfloat n00 = dot(g00, vec2(fx.x, fy.x));\n\tfloat n10 = dot(g10, vec2(fx.y, fy.y));\n\tfloat n01 = dot(g01, vec2(fx.z, fy.z));\n\tfloat n11 = dot(g11, vec2(fx.w, fy.w));\n\tvec2 fade_xy = fade(Pf.xy);\n\tvec2 n_x = mix(vec2(n00, n01), vec2(n10, n11), fade_xy.x);\n\tfloat n_xy = mix(n_x.x, n_x.y, fade_xy.y);\n\treturn 2.3 * n_xy;\n}\nfloat pnoise(vec2 P, vec2 rep) {\n\tvec4 Pi = floor(P.xyxy) + vec4(0.0, 0.0, 1.0, 1.0);\n\tvec4 Pf = fract(P.xyxy) - vec4(0.0, 0.0, 1.0, 1.0);\n\tPi = mod(Pi, rep.xyxy);\n\tPi = mod289(Pi);\n\tvec4 ix = Pi.xzxz;\n\tvec4 iy = Pi.yyww;\n\tvec4 fx = Pf.xzxz;\n\tvec4 fy = Pf.yyww;\n\tvec4 i = permute(permute(ix) + iy);\n\tvec4 gx = (fract(i * (1.0 / 41.0)) * 2.0) - 1.0;\n\tvec4 gy = abs(gx) - 0.5;\n\tvec4 tx = floor(gx + 0.5);\n\tgx = gx - tx;\n\tvec2 g00 = vec2(gx.x, gy.x);\n\tvec2 g10 = vec2(gx.y, gy.y);\n\tvec2 g01 = vec2(gx.z, gy.z);\n\tvec2 g11 = vec2(gx.w, gy.w);\n\tvec4 norm = taylorInvSqrt(vec4(dot(g00, g00), dot(g01, g01), dot(g10, g10), dot(g11, g11)));\n\tg00 *= norm.x;\n\tg01 *= norm.y;\n\tg10 *= norm.z;\n\tg11 *= norm.w;\n\tfloat n00 = dot(g00, vec2(fx.x, fy.x));\n\tfloat n10 = dot(g10, vec2(fx.y, fy.y));\n\tfloat n01 = dot(g01, vec2(fx.z, fy.z));\n\tfloat n11 = dot(g11, vec2(fx.w, fy.w));\n\tvec2 fade_xy = fade(Pf.xy);\n\tvec2 n_x = mix(vec2(n00, n01), vec2(n10, n11), fade_xy.x);\n\tfloat n_xy = mix(n_x.x, n_x.y, fade_xy.y);\n\treturn 2.3 * n_xy;\n}\nfloat fbm(vec2 P, int octaves, float lacunarity, float gain) {\n\tfloat sum = 0.0;\n\tfloat amp = 1.0;\n\tvec2 pp = P;\n\tfor (int i = 0; i < MAX_OCTAVES; i += 1) {\n\t\tamp *= gain;\n\t\tsum += (amp * cnoise(pp));\n\t\tpp *= lacunarity;\n\t\tif (i > octaves)\n\t\t\tbreak;\n\t}\n\treturn sum;\n}\nfloat pattern(in vec2 p) {\n\tfloat l = 2.5;\n\tfloat g = 0.4;\n\tint oc = int(octaves);\n\tvec2 q = vec2(fbm(p + vec2(0.0, 0.0), oc, l, g), fbm(p + vec2(5.2, 1.3), oc, l, g));\n\tvec2 r = vec2(fbm((p + (4.0 * q)) + vec2(1.7, 9.2), oc, l, g), fbm((p + (4.0 * q)) + vec2(8.3, 2.8), oc, l, g));\n\treturn fbm(p + (4.0 * r), oc, l, g);\n}\nfloat pattern2(in vec2 p, out vec2 r, in float time) {\n\tfloat l = 2.3;\n\tfloat g = 0.4;\n\tint oc = int(octaves);\n\tvec2 q;\n\tq.x = fbm(p + vec2(time, time), oc, l, g);\n\tq.y = fbm(p + vec2(5.2 * time, 1.3 * time), oc, l, g);\n\tr.x = fbm((p + (4.0 * q)) + vec2(1.7, 9.2), oc, l, g);\n\tr.y = fbm((p + (4.0 * q)) + vec2(8.3, 2.8), oc, l, g);\n\treturn fbm(p + (4.0 * r), oc, l, g);\n}\nvoid main() {\n\tvec2 q = v_uv.xz;\n\tvec2 r;\n\tfloat color = pattern2(q, r, u_time);\n\tvec3 realColor = vec3(.0, .0, .0);\n\tvec3 color1 = vec3(.8823, .9294, .7450);\n\tvec3 color2 = vec3(.8745, .7607, .9333);\n\tif (color >= .005) {\n\t\trealColor = mix(color2, vec3(color), 0.01);\n\t}\n\telse {\n\t\trealColor = mix(color1, vec3(color), 0.01);\n\t}\n\tvec4 f_color = vec4(realColor, u_alpha);\n\treturn f_color;\n}\n"